/* radio */
/* select */
.jq-checkbox { transition: all 0.3s linear; top: -1px; border: 1px solid #e6eaed; border-radius: 4px; vertical-align: middle; cursor: pointer; margin-right: 12px; width: 24px; height: 24px; background-color: #ffffff; }

.jq-checkbox .jq-checkbox__div { width: 20px; height: 20px; margin: 0px 0 0 0px; background: transparent; box-shadow: none; position: relative; }

.jq-checkbox .jq-checkbox__div:before { content: ""; width: 0px; background: #00aeef; height: 2px; border-radius: 2px; position: absolute; transform: rotate(45deg); top: 10px; left: 6px; transition: width 50ms ease 50ms; transform-origin: 0% 0%; }

.jq-checkbox .jq-checkbox__div:after { content: ""; width: 0; height: 2px; background: #00aeef; border-radius: 2px; position: absolute; transform: rotate(305deg); top: 14px; left: 9px; transition: width 50ms ease; transform-origin: 0% 0%; }

.jq-checkbox.checked .jq-checkbox__div:before { width: 7px; transition: width 100ms ease; }

.jq-checkbox.checked .jq-checkbox__div:after { width: 10px; transition: width 150ms ease 100ms; }

.jq-checkbox.disabled { opacity: 0.4; }

.jq-checkbox.disabled + span { opacity: 0.4; }

.jq-radio { width: 20px; height: 20px; border-radius: 50%; background: #fff; vertical-align: middle; cursor: pointer; float: left; border: 1px solid #C3C3C3; margin-right: 10px; }

.jq-radio.checked { border: solid 1px #a6ce39; }

.jq-radio.checked .jq-radio__div { width: 8px; height: 8px; margin: 5px 0 0 5px; background: #a6ce39; border-radius: 50%; }

.jq-radio.disabled { opacity: 0.5; }

.jq-file { width: 100%; height: 22px; line-height: 22px; font-size: 9px; color: #C4C4C4; display: inline-block; position: relative; overflow: hidden; }

.jq-file input { cursor: pointer; height: auto; line-height: 1em; position: absolute; top: 0px; right: 0px; margin: 0px; padding: 0px; opacity: 0; font-size: 100px; }

.jq-file__name { -moz-box-sizing: border-box; box-sizing: border-box; width: 100%; height: 22px; line-height: 22px; font-size: 9px; color: #C4C4C4; padding: 0px 10px 0px 0px; white-space: nowrap; text-overflow: ellipsis; text-align: center; overflow: hidden; box-shadow: none; background: transparent; outline: none; }

.jq-file__browse { position: absolute; top: 0px; right: 0px; padding: 0 0px; border-left: none; border-radius: 0; background: transparent; box-shadow: none; text-shadow: none; font-size: 18px; color: #B8C6D6; line-height: 1; height: 18px; width: 18px; }

.jq-file.disabled .jq-file__name { color: #00cccc; }

.jq-file.changed .jq-file__name { color: #000; }

.jq-file.disabled, .jq-file.disabled .jq-file__browse { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox { vertical-align: middle; cursor: pointer; width: 100%; display: inline-block; position: relative; z-index: 10; }

.jq-selectbox__select { height: 22px; padding: 4px 25px 0px 0px; border: 0px solid #2f0dfc; border-bottom: 1px solid #E3E3E3; background: transparent; font-size: 12px; line-height: 15px; color: #000; font-family: "Roboto", sans-serif; position: relative; }

.placeholder .jq-selectbox__select { font-size: 10px; color: #C4C4C4; }

.jq-selectbox.disabled .jq-selectbox__select { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-selectbox__select-text { display: block; width: 100%; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.jq-selectbox__trigger { position: absolute; top: 0; right: 0; width: 49px; height: 100%; }

.jq-selectbox__trigger-arrow { position: absolute; top: 0px; right: 0px; width: 12px; height: 7px; }

.jq-selectbox__trigger-arrow:after { content: ''; display: block; position: absolute; top: 8px; right: 8px; transform: rotate(45deg); width: 6px; height: 6px; border-bottom: 1px solid #284DC0; border-right: 1px solid #284DC0; }

.jq-selectbox.opened .jq-selectbox__trigger-arrow:after { transform: rotate(-135deg); }

.jq-selectbox__dropdown { position: absolute; display: none; top: 22px; width: 100%; box-sizing: border-box; margin: 0; padding: 0; border: 2px solid #2f0dfc; border-radius: 0px; background: #000; line-height: 20px; padding: 0px 0; font-size: 12px; color: #000; font-size: 10px; color: #C4C4C4; background: #FFFFFF; border: 1px solid #E3E3E3; box-sizing: border-box; box-shadow: 0px 8px 14px rgba(0, 0, 0, 0.2); border-radius: 0px 0px 8px 8px; }

.jq-selectbox__search { margin: 5px; }

.jq-selectbox__search input { -moz-box-sizing: border-box; box-sizing: border-box; width: 100%; margin: 0; padding: 5px 27px 6px 8px; outline: none; border: 1px solid #CCC; border-radius: 3px; background: url(data:image/png;base64,) no-repeat 100% 50%; box-shadow: inset 1px 1px #F1F1F1; color: #333; -webkit-appearance: textfield; }

.jq-selectbox__search input::-webkit-search-cancel-button, .jq-selectbox__search input::-webkit-search-decoration { -webkit-appearance: none; }

.jq-selectbox__not-found { margin: 5px; padding: 5px 8px 6px; background: #F0F0F0; font-size: 13px; }

.jq-selectbox ul { position: relative; list-style: none; overflow: auto; overflow-x: hidden; margin: 0; padding: 0; }

.jq-selectbox li { min-height: 18px; position: relative; padding: 0px 10px; }

.jq-selectbox li.selected, .jq-selectbox li.sel { color: #000; background: #E8EDF4; }

.jq-selectbox li.placeholder { display: none !important; }

.jq-selectbox li:hover { color: #000; background: #E8EDF4; }

.jq-selectbox li.disabled { color: #ccc; background: none; }

.jq-selectbox li.disabled:hover { background: none; }

.jq-selectbox li.optgroup { font-weight: bold; }

.jq-selectbox li.optgroup:hover { background: none; color: #231F20; cursor: default; }

.jq-selectbox li.option { padding-left: 25px; }

.jq-select-multiple { -moz-box-sizing: border-box; box-sizing: border-box; padding: 1px; border: 1px solid #CCC; border-bottom-color: #B3B3B3; border-radius: 4px; box-shadow: inset 1px 1px #F1F1F1, 0 1px 2px rgba(0, 0, 0, 0.1); background: #FFF; color: #333; font: 14px/18px Arial, sans-serif; cursor: default; }

.jq-select-multiple.focused { border: 1px solid #5794BF; }

.jq-select-multiple.disabled { border-color: #CCC; background: #F5F5F5; box-shadow: none; color: #888; }

.jq-select-multiple ul { margin: 0; padding: 0; }

.jq-select-multiple li { padding: 3px 9px 4px; list-style: none; }

.jq-select-multiple li:first-child { border-radius: 3px 3px 0 0; }

.jq-select-multiple li:last-child { border-radius: 0 0 3px 3px; }

.jq-select-multiple li.selected { background: #08C; color: #FFF; }

.jq-select-multiple li.disabled { color: #AAA; }

.jq-select-multiple.disabled li.selected, .jq-select-multiple li.selected.disabled { background: #CCC; color: #FFF; }

.jq-select-multiple li.optgroup { font-weight: bold; }

.jq-select-multiple li.option { padding-left: 25px; }

